<template>
  <v-layout class="full-height" column align-center>
        <v-form v-model="isValid">
            <v-text-field
            color="#fb7600"
                label="Email"
                v-model="email"
                :rules="[notEmptyRule, emailRule]"/>
            <v-text-field
            color="#fb7600"
                label="Repeat Email"
                v-model="emailRepeated"
                :rules="[notEmptyRule, sameEmailRule]"/>
            <v-text-field
            color="#fb7600"
                label="Password"
                type="password"
                v-model="password"
                :rules="[notEmptyRule, lengthRule]"/>
            <v-text-field
          color="#fb7600"
                label="Repeat Password"
                type="password"
                v-model="passwordRepeated"
                :rules="[notEmptyRule, samePasswordRule]"/>
        </v-form>
        <v-btn 
          color="#fb7600"
          dark
          :disabled="!isValid"
          v-if="!isLoading"
          @click="signUp">
          Sign Up
        </v-btn>
        <v-progress-circular
          v-else
          :size="70"
          :width="7"
          color="#fb7600"
          indeterminate
        />
  </v-layout>
</template>

<script>
import store from "@/store";
import { mapState } from "vuex";
import { SIGN_UP } from "../vuex/actions.js";

export default {
  name: "signup",
  data: function() {
    return {
      isValid: false,
      email: "",
      emailRepeated: "",
      password: "",
      passwordRepeated: "",
      notEmptyRule: v => v.length > 0 || "Can't be empty",
      lengthRule: v => v.length > 3 || `Must be longer than 3 characters`,
      emailRule: v => /\S+@\S+/.test(v) || "Must be valid email",
      samePasswordRule: v => v === this.password || "Password are different",
      sameEmailRule: v => v === this.email || "Emails are different"
    };
  },
  computed: mapState(["isLoading"]),
  methods: {
    signUp() {
      store.dispatch(SIGN_UP, {
        email: this.email,
        password: this.password
      });
    }
  }
};
</script>

<style lang="sass" scoped>
  .full-height
    min-height: 100%
    justify-content: center
</style>